
// ─── 调色板 ───────────────────────────────────────
$bg: #0d1117;
$bg-panel: #111820;
$border: #1e2a38;
$border-accent: #2a3f55;
$red: #c0392b;
$red-light: #e74c3c;
$gold: #c9a227;
$gold-light: #f0c040;
$text-primary: #d4dce8;
$text-muted: #6b7f94;
$text-label: #8896a8;
$fill-high: #27ae60;
$fill-mid: #c9a227;
$fill-low: #7f8c8d;

// ─── 容器 ────────────────────────────────────────
.status-bar {
  width: 100%;
  background: $bg;
  color: $text-primary;
  font-size: 11px;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
  gap: 1px;
}

// ─── 面板 ────────────────────────────────────────
.panel {
  background: $bg-panel;
  border-left: 2px solid $border-accent;
  padding: 6px 8px;
}

.panel--erotic {
  border-left-color: $red;
  background: mix($bg-panel, $red, 94%);
}

// ─── 面板标题 ────────────────────────────────────
.panel-header {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: $text-muted;
  border-bottom: 1px solid $border;
  padding-bottom: 4px;
  margin-bottom: 5px;

  i {
    font-size: 9px;
    color: $gold;
  }
}

.panel-header--red {
  color: $red-light;

  i {
    color: $red-light;
  }
}

// ─── 信息网格 ─────────────────────────────────────
.info-grid {
  display: grid;
  grid-template-columns: 4em 1fr;
  gap: 2px 6px;
  align-items: baseline;
}

.label {
  color: $text-label;
  font-size: 10px;
  white-space: nowrap;
  text-align: right;
}

.value {
  color: $text-primary;
  word-break: break-all;

  &.gold { color: $gold-light; font-weight: 600; }
  &.red  { color: $red-light; }
  &.accent { color: $gold; }
}

// ─── 单行附加信息 ─────────────────────────────────
.sub-row {
  display: flex;
  gap: 6px;
  align-items: baseline;
  margin-top: 3px;

  .label {
    min-width: 4em;
    text-align: right;
    flex-shrink: 0;
  }

  .value {
    flex: 1;
    word-break: break-all;
  }
}

// ─── 场景描述 ─────────────────────────────────────
.desc-text {
  margin-top: 4px;
  padding: 4px 6px;
  border-left: 2px solid $border-accent;
  color: $text-muted;
  font-style: italic;
  font-size: 10px;
  line-height: 1.5;
}

// ─── 能力条 ───────────────────────────────────────
.abilities {
  margin-top: 5px;
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.ability-row {
  display: grid;
  grid-template-columns: 4.5em 1fr 2.2em;
  align-items: center;
  gap: 5px;
}

.ability-name {
  color: $text-label;
  font-size: 10px;
  text-align: right;
  white-space: nowrap;
}

.ability-track {
  height: 4px;
  background: $border;
  border-radius: 2px;
  overflow: hidden;
}

.ability-fill {
  height: 100%;
  border-radius: 2px;
  transition: width 0.3s ease;

  &.fill--high { background: $fill-high; }
  &.fill--mid  { background: $fill-mid; }
  &.fill--low  { background: $fill-low; }
}

.ability-num {
  color: $text-muted;
  font-size: 10px;
  text-align: right;
}

// ─── 绯色状态 ─────────────────────────────────────
.erotic-person {
  & + & {
    margin-top: 6px;
    padding-top: 6px;
    border-top: 1px dashed $border-accent;
  }
}

.erotic-name {
  color: $red-light;
  font-weight: 600;
  font-size: 10px;
  margin-bottom: 3px;
}

.special-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  margin-top: 4px;
}

.state-tag {
  display: inline-block;
  padding: 1px 5px;
  background: mix($bg-panel, $red, 80%);
  border: 1px solid mix($border, $red, 60%);
  border-radius: 2px;
  color: $red-light;
  font-size: 9px;
}
